import React, { Component } from 'react';
import Button from '@material-ui/core/Button';
import ListContent from '../ListContent';
import './OnlineConsultation.css';

class OnlineConsultation extends Component {

  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 0,
      consultationsEnabled: false
    };
  }

  getData() {
      return {
        'Educación nutricional': this.getDetails(['¿Tu alimentación no es mala pero te gustaría seguir mejorándola?', 'Desde la educación nutricional te ayudaré a comer saludable disfrutando de la comida y evitando la monotonía', 'Te acompañaré en el camino del cambio de hábitos aportándote las herramientas necesarias para ello']),
        'Sobrepeso y obesidad': this.getDetails(['Buscaremos alcanzar la pérdida de grasa desde la educación nutricional evitando las "dietas" restrictivas, monótonas y de cajón', 'El peso no lo es todo, aprenderemos a buscar otras referencias que nos permitan ver los cambios obtenidos sin centrarnos unicamente en la báscula']),
        'Nutrición clínica': this.getDetails(['¿Sabías que tus problemas de salud pueden mejorar adaptando tu alimentación?', 'Si tienes problemas renales, digestivos, cardiovasculares, disfagia...  puedo ayudarte', 'Te ayudaré a sentirte mejor, mejorar los parámetros analíticos y los riesgos asociados a tu enfermedad', 'Mi especialidad es la patología renal']),
        'Etapas de la vida': this.getDetails(['Son etapas de la vida el embarazo, infancia, vejez... mi predilección es la vejez', 'Entre las personas mayores  a menudo aparece deterioro cognitivo unido a falta de apetito, dificultades para tragar, disfagia... que solemos pasar por alto ', 'Si eres cuidador de una persona mayor y no sabes cómo abordar su alimentación puedo ayudarte']),
        'Alimentación vegetariana y vegana': this.getDetails(['¿Quieres cambiar tu alimentación a una dieta vegetariana/vegana y no sabes cómo?', 'Te ayudaré a adaptar tu alimentación a esta pauta asegurando que se incluyan todos los nutrientes necesarios'])
      }
  }

  getDetails(list) {
    return( 
      <ul>
        { list.map((text) => <li>{ text }</li>) }
      </ul>
      );
  }

  enableConsultations(enable) {
    const consultationsView = document.querySelector('.Consultations')
    if (enable) {
      consultationsView.style.height = '50vh';
    } else {
      consultationsView.style.height = '0vh';
    }
  }

  render() {
    return (
      <div className='consultation-container'>
      <div className='title'><p>¿Necesitas ayuda para mejorar tu alimentación?</p><p>Puedo ayudarte:</p></div>
        <div className='info columns'>
          <div className='main column'>
            <ListContent data={ this.getData() } />
            <div className='actions'>
              <Button size='large' variant='contained' color={ this.state.consultationsEnabled ? 'secondary' : 'primary' } 
                onClick={ () => {
                  this.enableConsultations(!this.state.consultationsEnabled);
                  this.setState({consultationsEnabled: !this.state.consultationsEnabled});
                } 
              }>
                + info consultas/Tarifas
              </Button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default OnlineConsultation;
